import React, { useState } from 'react';
import { Users, Heart, Activity, Calendar, MessageCircle, Bell, Plus, UserPlus, Phone, Video } from 'lucide-react';
import { toast } from 'react-toastify';

const CaregiverDashboard = () => {
  const [showAddPatient, setShowAddPatient] = useState(false);
  
  
  // Mock data - will be replaced with real data from Data Connect
  const [patients, setPatients] = useState([
    {
      id: 1,
      name: 'John Smith',
      age: 78,
      relationship: 'Father',
      lastSeen: '2 hours ago',
      status: 'stable',
      medications: 3,
      appointments: 2,
      vitalSigns: {
        bloodPressure: '120/80',
        heartRate: '72',
        temperature: '98.6'
      },
      alerts: 0
    },
    {
      id: 2,
      name: 'Mary Johnson',
      age: 82,
      relationship: 'Mother',
      lastSeen: '1 day ago',
      status: 'attention',
      medications: 5,
      appointments: 1,
      vitalSigns: {
        bloodPressure: '140/90',
        heartRate: '85',
        temperature: '99.2'
      },
      alerts: 2
    }
  ]);

  const [newPatient, setNewPatient] = useState({
    name: '',
    age: '',
    relationship: 'Family Member',
    email: '',
    phone: ''
  });

  const handleAddPatient = (e) => {
    e.preventDefault();
    const patient = {
      id: Date.now(),
      ...newPatient,
      lastSeen: 'Just now',
      status: 'stable',
      medications: 0,
      appointments: 0,
      vitalSigns: {
        bloodPressure: '--/--',
        heartRate: '--',
        temperature: '--'
      },
      alerts: 0
    };
    setPatients([...patients, patient]);
    setNewPatient({
      name: '',
      age: '',
      relationship: 'Family Member',
      email: '',
      phone: ''
    });
    setShowAddPatient(false);
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'stable': return 'bg-green-100 text-green-800';
      case 'attention': return 'bg-yellow-100 text-yellow-800';
      case 'urgent': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getStatusIcon = (status) => {
    switch (status) {
      case 'stable': return <Heart className="h-4 w-4 text-green-600" />;
      case 'attention': return <Bell className="h-4 w-4 text-yellow-600" />;
      case 'urgent': return <Bell className="h-4 w-4 text-red-600" />;
      default: return <Heart className="h-4 w-4 text-gray-600" />;
    }
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Caregiver Dashboard</h1>
          <p className="text-gray-600">Monitor and manage your patients' health</p>
        </div>
        <button 
          onClick={() => setShowAddPatient(true)}
          className="btn btn-primary"
        >
          <UserPlus className="h-4 w-4" />
          Add Patient
        </button>
      </div>

      {/* Add Patient Form */}
      {showAddPatient && (
        <div className="card">
          <h2 className="text-lg font-semibold text-gray-900 mb-4">Add New Patient</h2>
          <form onSubmit={handleAddPatient} className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="form-label">Patient Name</label>
                <input
                  type="text"
                  className="form-input"
                  value={newPatient.name}
                  onChange={(e) => setNewPatient({...newPatient, name: e.target.value})}
                  placeholder="Full name"
                  required
                />
              </div>
              <div>
                <label className="form-label">Age</label>
                <input
                  type="number"
                  className="form-input"
                  value={newPatient.age}
                  onChange={(e) => setNewPatient({...newPatient, age: e.target.value})}
                  placeholder="Age"
                  required
                />
              </div>
              <div>
                <label className="form-label">Relationship</label>
                <select
                  className="form-input"
                  value={newPatient.relationship}
                  onChange={(e) => setNewPatient({...newPatient, relationship: e.target.value})}
                >
                  <option value="Family Member">Family Member</option>
                  <option value="Parent">Parent</option>
                  <option value="Spouse">Spouse</option>
                  <option value="Friend">Friend</option>
                  <option value="Client">Client</option>
                </select>
              </div>
              <div>
                <label className="form-label">Email</label>
                <input
                  type="email"
                  className="form-input"
                  value={newPatient.email}
                  onChange={(e) => setNewPatient({...newPatient, email: e.target.value})}
                  placeholder="patient@example.com"
                />
              </div>
              <div>
                <label className="form-label">Phone</label>
                <input
                  type="tel"
                  className="form-input"
                  value={newPatient.phone}
                  onChange={(e) => setNewPatient({...newPatient, phone: e.target.value})}
                  placeholder="(555) 123-4567"
                />
              </div>
            </div>
            <div className="flex space-x-4">
              <button type="submit" className="btn btn-primary">
                Add Patient
              </button>
              <button 
                type="button" 
                onClick={() => setShowAddPatient(false)}
                className="btn btn-outline"
              >
                Cancel
              </button>
            </div>
          </form>
        </div>
      )}

      {/* Overview Stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="card">
          <div className="flex items-center">
            <Users className="h-8 w-8 text-blue-600" />
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-500">Total Patients</p>
              <p className="text-2xl font-bold text-gray-900">{patients.length}</p>
            </div>
          </div>
        </div>
        <div className="card">
          <div className="flex items-center">
            <Bell className="h-8 w-8 text-yellow-600" />
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-500">Active Alerts</p>
              <p className="text-2xl font-bold text-gray-900">
                {patients.reduce((sum, patient) => sum + patient.alerts, 0)}
              </p>
            </div>
          </div>
        </div>
        <div className="card">
          <div className="flex items-center">
            <Calendar className="h-8 w-8 text-green-600" />
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-500">Upcoming Appointments</p>
              <p className="text-2xl font-bold text-gray-900">
                {patients.reduce((sum, patient) => sum + patient.appointments, 0)}
              </p>
            </div>
          </div>
        </div>
        <div className="card">
          <div className="flex items-center">
            <Activity className="h-8 w-8 text-purple-600" />
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-500">Medications Tracked</p>
              <p className="text-2xl font-bold text-gray-900">
                {patients.reduce((sum, patient) => sum + patient.medications, 0)}
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Patients List */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {patients.map((patient) => (
          <div key={patient.id} className="card">
            <div className="flex items-start justify-between mb-4">
              <div className="flex items-center">
                <div className="w-12 h-12 bg-blue-100 rounded-full flex items-center justify-center">
                  <Users className="h-6 w-6 text-blue-600" />
                </div>
                <div className="ml-4">
                  <h3 className="text-lg font-semibold text-gray-900">{patient.name}</h3>
                  <p className="text-sm text-gray-500">Age {patient.age}  {patient.relationship}</p>
                </div>
              </div>
              <div className="flex items-center space-x-2">
                <span className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(patient.status)}`}>
                  {getStatusIcon(patient.status)}
                  <span className="ml-1">{patient.status}</span>
                </span>
                {patient.alerts > 0 && (
                  <div className="w-6 h-6 bg-red-500 rounded-full flex items-center justify-center">
                    <span className="text-xs text-white font-bold">{patient.alerts}</span>
                  </div>
                )}
              </div>
            </div>

            {/* Vital Signs */}
            <div className="grid grid-cols-3 gap-4 mb-4">
              <div className="text-center">
                <p className="text-xs text-gray-500">Blood Pressure</p>
                <p className="text-sm font-semibold text-gray-900">{patient.vitalSigns.bloodPressure}</p>
              </div>
              <div className="text-center">
                <p className="text-xs text-gray-500">Heart Rate</p>
                <p className="text-sm font-semibold text-gray-900">{patient.vitalSigns.heartRate} bpm</p>
              </div>
              <div className="text-center">
                <p className="text-xs text-gray-500">Temperature</p>
                <p className="text-sm font-semibold text-gray-900">{patient.vitalSigns.temperature}F</p>
              </div>
            </div>

            {/* Quick Stats */}
            <div className="flex items-center justify-between text-sm text-gray-500 mb-4">
              <div className="flex items-center">
                <Activity className="h-4 w-4 mr-1" />
                {patient.medications} medications
              </div>
              <div className="flex items-center">
                <Calendar className="h-4 w-4 mr-1" />
                {patient.appointments} appointments
              </div>
              <div className="flex items-center">
                <Bell className="h-4 w-4 mr-1" />
                Last seen {patient.lastSeen}
              </div>
            </div>

            {/* Action Buttons */}
            <div className="flex space-x-2">
              <button className="btn btn-outline flex-1">
                <MessageCircle className="h-4 w-4 mr-2" />
                Message
              </button>
              <button className="btn btn-outline">
                <Phone className="h-4 w-4" />
              </button>
              <button className="btn btn-outline">
                <Video className="h-4 w-4" />
              </button>
            </div>
          </div>
        ))}
      </div>

      {/* Empty State */}
      {patients.length === 0 && (
        <div className="text-center py-12">
          <Users className="h-12 w-12 text-gray-400 mx-auto mb-4" />
          <h3 className="text-lg font-medium text-gray-900 mb-2">No patients added yet</h3>
          <p className="text-gray-500 mb-4">Start by adding your first patient to monitor their health</p>
          <button 
            onClick={() => setShowAddPatient(true)}
            className="btn btn-primary"
          >
            <UserPlus className="h-4 w-4 mr-2" />
            Add Your First Patient
          </button>
        </div>
      )}
    </div>
  );
};

export default CaregiverDashboard;
